import React, { useEffect, useState } from 'react';
import { useNavigate } from "react-router-dom";
import firebase from "../../firebase.js";

const UserLogin = () => {
    const [email, setEmail] = useState("");
    const [password, setPassword] = useState("");
    const [errorMsg, setErrorMsg] = useState("");
    const navigate = useNavigate();

    const LoginFunc = async (e) => {
        e.preventDefault();

        if (!(email && password)) {
            return alert("   !");
        }
        try {
            await firebase.auth().signInWithEmailAndPassword(email, password);
            alert(" .");
            navigate("/");
        } catch (err) {
            console.log(err);
            setErrorMsg("    !")
        }
    }

    useEffect(() => {
        setTimeout(() => {
            setErrorMsg("")
        }, 5000)
    }, [errorMsg]);

    return (
        <div className='login__wrap'>
            <div className="login__header">
                <h3>login</h3>
                <p> ?!</p>
            </div>
            <form className='login__form'>
                <fieldset>
                    <legend className="blind"> </legend>
                    <div>
                        <label htmlFor="youEmail" className="required blind"></label>
                        <input
                            type="email"
                            id="youEmail"
                            name="youEmail"
                            placeholder=""
                            className="input__style"
                            autoComplete='off'
                            required
                            value={email}
                            onChange={(e) => setEmail(e.currentTarget.value)}
                        />
                    </div>
                    <div>
                        <label htmlFor="youPass" className="required blind"></label>
                        <input
                            type="password"
                            id="youPass"
                            name="youPass"
                            placeholder=""
                            className="input__style"
                            autoComplete="off"
                            required
                            value={password}
                            onChange={(e) => setPassword(e.currentTarget.value)}
                        />
                    </div>
                    <div>
                        {errorMsg !== "" && <p>{errorMsg}</p>}
                    </div>
                    <button type="submit" onClick={(e) => LoginFunc(e)} className="btn__style2 mt30"></button>
                </fieldset>
            </form>
        </div>
    )
}

export default UserLogin